import React, { useState } from 'react';
import { Input, Button, Row, Col, Radio } from 'antd';
import { Container, Typography, Box } from '@material-ui/core';
import data from '../data/cj';

export default function Index() {
    const [lorem, setLorem] = useState([]);
    const [qtd, setQtd] = useState(5);
    const [type, setType] = useState('phrases');

    function handleGenerate() {
        let loremArray = [];
        let qtdParagraphs;

        if (type === 'phrases') {
            qtdParagraphs = 1;
        }
        else {
            qtdParagraphs = qtd;
        }

        for (let i = 0; i < qtdParagraphs; i++) {
            let loremP = [];
            let qtdPhrases = type === 'phrases' ? qtd : Math.floor(Math.random() * (30 - 5)) + 5;

            for (let j = 0; j < qtdPhrases; j++) {
                let random = data[Math.floor(Math.random() * data.length)];
                loremP = [...loremP, random];
            }

            const joined = loremP.join(' ');

            loremArray = [...loremArray, joined];
        }

        setLorem(loremArray);
    }

    return (
        <Container>
            <Typography variant="h4" style={{ marginBottom: 15 }}>CJ-Ipsum</Typography>
            <Typography variant="h4" style={{ marginBottom: 15 }}>Quantity?</Typography>
            <Box display='flex' alignItems='center'>
                <Radio.Group onChange={e => setType(e.target.value)} value={type}>
                    <Radio
                        style={{
                            display: 'block',
                            height: '30px',
                            lineHeight: '30px',
                        }}
                        value={'phrases'}>
                        <span>Phrases</span>
                    </Radio>
                    <Radio
                        style={{
                            display: 'block',
                            height: '30px',
                            lineHeight: '30px',
                        }}
                        value={'paragraphs'}>
                        <span>Paragraphs</span>
                    </Radio>
                </Radio.Group>
                <Box display='flex'>
                    <Input maxLength="3" onChange={e => setQtd(e.target.value)} value={qtd} />
                    <Button onClick={() => handleGenerate()}>Generate</Button>
                </Box>

            </Box>

            {lorem.map((paragraph, index) => (
                <Typography
                    key={index.toString()}
                    variant="body1"
                    style={{ marginTop: 50, color: '#000', fontSize: 22, textAlign: 'justify' }}>
                    {paragraph}
                </Typography>
            ))}

        </Container>
    );
}
